import { makeStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import IconButton from '@material-ui/core/IconButton';
import Typography from '@material-ui/core/Typography';
import AppsIcon from '@material-ui/icons/Apps';
import ArrowBackIcon from '@material-ui/icons/ArrowBack';
import useScrollTrigger from '@material-ui/core/useScrollTrigger';
import { useSecret } from '@src/hooks';
import { ThemeToggler } from './ThemeToggler';

const useStyles = makeStyles(theme => ({
  toolbar: {
    paddingLeft: theme.spacing(1),
    paddingRight: theme.spacing(1),
  },
  title: {
    flexGrow: 1,
    marginLeft: theme.spacing(1),
  },
  section: {
    marginRight: theme.spacing(2),
  },
  pbr: {
    marginRight: theme.spacing(2),
  },

  mainTitle: {
    'fontWeight': theme.typography.fontWeightBold,
    '& > sup': {
      fontSize: '80%',
    },
  },
  subTitle: {
    verticalAlign: 'top',
  },
  offset: theme.mixins.toolbar,
}));

/**
 * @param {object} props
 * @param {string} props.title 标题
 * @param {CourseEntity} [props.course] 课程对象
 * @param {React.ReactNode} [props.actions] 可选操作
 * @param {() => void} [props.goBack] 返回回调函数
 */
export function TitleBar({ title, course, actions, goBack }) {
  const classes = useStyles();
  const { knowSecret } = useSecret();

  const trigger = useScrollTrigger({
    disableHysteresis: true,
    threshold: 0,
  });

  const revealCourseBrief = useScrollTrigger({
    disableHysteresis: true,
    threshold: 80,
  });

  return (
    <>
      <AppBar position="fixed" elevation={trigger ? 4 : 0}>
        <Toolbar variant="regular" className={classes.toolbar}>
          {!goBack && (
            <IconButton color="inherit" disableRipple>
              <AppsIcon />
            </IconButton>
          )}
          {Boolean(goBack && knowSecret) && (
            <IconButton color="inherit" onClick={goBack}>
              <ArrowBackIcon />
            </IconButton>
          )}
          <Typography variant="inherit" display="block" noWrap className={classes.title}>
            <Typography variant="h6" color="inherit" className={classes.mainTitle}>
              {title}
            </Typography>
            {Boolean(revealCourseBrief && course) && (
              <Typography variant={'caption'} color="inherit" className={classes.subTitle}>
                {course.title}
              </Typography>
            )}
          </Typography>

          {/* 操作相关的图标 */}
          {actions}

          <ThemeToggler />
        </Toolbar>
      </AppBar>

      {/* @see https://material-ui.com/components/app-bar/#fixed-placement */}
      <div className={classes.offset} />
    </>
  );
}
