import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Accordion from '@material-ui/core/Accordion';
import AccordionSummary from '@material-ui/core/AccordionSummary';
import AccordionDetails from '@material-ui/core/AccordionDetails';
import Grid from '@material-ui/core/Grid';
import Typography from '@material-ui/core/Typography';
import { AudioCard } from './AudioCard';

const useStyles = makeStyles(theme => ({
  summary: {
    'cursor': 'default !important',
    'minHeight': 'auto !important',
    'margin': '0 8px !important',
    'padding': '0 4px',

    '& > .Mui-expanded': {
      margin: '10px 0',
    },
  },
  heading: {
    fontSize: theme.typography.pxToRem(14),
    fontWeight: theme.typography.fontWeightBold,
    borderLeft: `3px solid ${theme.palette.secondary.main}`,
    paddingLeft: 8,
  },
  details: {
    padding: 0,
  },
}));

/**
 * @param {object} props
 * @param {SectionEntity} props.section
 */
export function Section({ section }) {
  const classes = useStyles();

  return (
    <Accordion expanded={true} elevation={1} square>
      {section.sectionTitle !== '-1' && (
        <AccordionSummary className={classes.summary}>
          <Typography className={classes.heading}>{section.sectionTitle}</Typography>
        </AccordionSummary>
      )}
      <AccordionDetails className={classes.details}>
        <Grid container spacing={0}>
          <Grid item xs={12} sm={12}>
            {section.list.map(audio => (
              <AudioCard key={audio.audioPid} audio={audio} />
            ))}
          </Grid>
        </Grid>
      </AccordionDetails>
    </Accordion>
  );
}
