import { useMemo, useCallback } from 'react';
import { useGlobalState } from '@src/components';

/**
 * @param {object} options
 * @param {CourseEntity} options.course
 * @param {SectionEntity[]} options.sectionList
 */
export function useCourseSection({ course, sectionList }) {
  const [globalState, dispathAction] = useGlobalState();
  const { selectedSectionIdMap } = globalState;

  const selectedSectionId = useMemo(() => {
    const courseId = course.id;

    let sectionId = selectedSectionIdMap[courseId];
    if (!sectionList.find(x => x.sectionPid === sectionId)) {
      sectionId = 'all';
    }

    return sectionId;
  }, [course, sectionList, selectedSectionIdMap]);

  const updateSelectedSectionId = useCallback(
    (/** @type {string} */ sectionId) => {
      dispathAction({
        type: 'updateSelectedSectionId',
        payload: { courseId: course.id, sectionId },
      });
    },
    [course, dispathAction],
  );

  return { selectedSectionId, updateSelectedSectionId };
}
