import { useMemo, useEffect, useCallback } from 'react';
import { useMount, useUnmount, useUpdateEffect } from 'react-use';
import { soundManager } from 'soundmanager2';
import { makeStyles } from '@material-ui/core/styles';
import { EE } from '@src/utils';
import { useGlobalState } from '@src/components';
import { useCourseSection } from '@src/hooks';
import { Section } from './Section';

const useStyles = makeStyles(theme => ({
  root: {
    paddingTop: theme.spacing(2),
    paddingBottom: theme.spacing(2),
  },
}));

/**
 * @param {object} props
 * @param {CourseEntity} props.course
 * @param {SectionEntity[]} props.sectionList
 */
export function SectionList({ course, sectionList }) {
  const classes = useStyles();
  const [globalState, dispathAction] = useGlobalState();
  const { selectedSectionId } = useCourseSection({ course, sectionList });
  const { playingMediaId } = globalState;

  useMount(() => {
    // @see https://github.com/leoasis/react-sound
    soundManager.setup({ ignoreMobileRestrictions: true });
  });

  useUnmount(() => {
    dispathAction({ type: 'switchPlayingMediaId', payload: { id: null } });
  });

  const filteredSectionList = useMemo(() => {
    if (selectedSectionId === 'all') {
      return sectionList;
    }
    return sectionList.filter(x => x.sectionPid === selectedSectionId);
  }, [sectionList, selectedSectionId]);

  const audioList = useMemo(() => {
    return filteredSectionList.map(section => section.list).flat();
  }, [filteredSectionList]);

  const replyNextPlayingMediaId = useCallback(() => {
    const currentIndex = audioList.findIndex(x => x.audioPid === playingMediaId);

    // 没有下一音频
    if (currentIndex + 1 >= audioList.length) {
      dispathAction({ type: 'switchPlayingMediaId', payload: { id: null } });
      return;
    }

    const nextPlayingMediaId = audioList[currentIndex + 1]?.audioPid;
    EE.emit('replyNextPlayingMediaId', { id: nextPlayingMediaId });
  }, [playingMediaId, dispathAction, audioList]);

  useUpdateEffect(() => {
    if (playingMediaId === null) {
      return;
    }
    if (!audioList.find(x => x.audioPid === playingMediaId)) {
      dispathAction({ type: 'switchPlayingMediaId', payload: { id: null } });
    }
  }, [playingMediaId, audioList, dispathAction]);

  useEffect(() => {
    EE.on('requestNextPlayingMediaId', replyNextPlayingMediaId);

    return () => {
      EE.off('requestNextPlayingMediaId', replyNextPlayingMediaId);
    };
  }, [replyNextPlayingMediaId]);

  return (
    <div className={classes.root}>
      {filteredSectionList.map(section => (
        <Section key={section.sectionPid} section={section} />
      ))}
    </div>
  );
}
