import React, { createContext, useReducer, useContext } from 'react';
import { localStore } from '@src/utils';
import { DEFAULT_PLAYBACK_RATE, PLAYBACK_RATE_STORED_KEY } from '@src/configs/pbr';
import {
  COURSE_KEYWORD_MAP_STORED_KEY,
  SELECTED_SECTION_ID_MAP_STORED_KEY,
} from '@src/configs/courses/env';

/**
 * @param {GlobalState} state
 * @param {GlobalAction} action
 * @returns {GlobalState}
 */
const globalReducer = (state, action) => {
  const { type, payload } = action;

  switch (type) {
    case 'updatePlaybackRate': {
      const playbackRate = payload['value'];
      localStore.set(PLAYBACK_RATE_STORED_KEY, playbackRate);
      return { ...state, playbackRate };
    }

    case 'updateSelectedSectionId': {
      const courseId = payload['courseId'];
      const sectionId = payload['sectionId'];
      const selectedSectionIdMap = { ...state.selectedSectionIdMap };

      if (sectionId !== 'all') {
        selectedSectionIdMap[courseId] = sectionId;
      } else {
        delete selectedSectionIdMap[courseId];
      }

      localStore.set(SELECTED_SECTION_ID_MAP_STORED_KEY, selectedSectionIdMap);
      return { ...state, selectedSectionIdMap };
    }

    case 'switchPlayingMediaId': {
      return { ...state, playingMediaId: payload['id'] || null };
    }

    case 'updateCourseKeyword': {
      const platform = payload['platform'];
      const keyword = String(payload['keyword']).trim();
      const courseKeywordMap = { ...state.courseKeywordMap };

      if (keyword) {
        courseKeywordMap[platform] = keyword;
      } else {
        delete courseKeywordMap[platform];
      }

      localStore.set(COURSE_KEYWORD_MAP_STORED_KEY, courseKeywordMap);
      return { ...state, courseKeywordMap };
    }

    default:
      return state;
  }
};

const GlobalStateContext = createContext(null);

/** @type {GlobalState} */
const intialState = {
  playbackRate: DEFAULT_PLAYBACK_RATE,
  selectedSectionIdMap: (() => {
    return localStore.get(SELECTED_SECTION_ID_MAP_STORED_KEY) || {};
  })(),
  playingMediaId: null,
  courseKeywordMap: (() => {
    return localStore.get(COURSE_KEYWORD_MAP_STORED_KEY) || {};
  })(),
};

/**
 * @type {() => [GlobalState, React.Dispatch<GlobalAction>]}
 */
export const useGlobalState = () => useContext(GlobalStateContext);

export function GlobalStateProvider({ children }) {
  return (
    <GlobalStateContext.Provider value={useReducer(globalReducer, intialState)}>
      {children}
    </GlobalStateContext.Provider>
  );
}
