import { useMemo } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';
import Typography from '@material-ui/core/Typography';
import { courseCollection } from '@src/configs/courses/vistopia';
import { useCourseKeyword } from '@src/hooks';
import { CourseCard } from './CourseCard';

const useStyles = makeStyles(theme => ({
  root: {
    paddingTop: theme.spacing(1),
    paddingBottom: theme.spacing(1),
  },
  noResult: {
    textAlign: 'center',
    marginTop: theme.spacing(2),
  },
}));

export function CourseList() {
  const classes = useStyles();
  const { courseKeyword, getCourseEntityList } = useCourseKeyword({
    platform: 'vistopia',
    courseCollection,
  });

  const courseEntityList = useMemo(() => getCourseEntityList(courseKeyword), [
    courseKeyword,
    getCourseEntityList,
  ]);

  return (
    <Grid container spacing={1} alignItems="stretch" className={classes.root}>
      {courseEntityList.length === 0 && (
        <Grid item xs={12}>
          <Typography
            variant="inherit"
            component="div"
            color="textSecondary"
            className={classes.noResult}
          >
            {'未搜索到课程'}
          </Typography>
        </Grid>
      )}
      {courseEntityList.length > 0 && (
        <>
          {courseEntityList.map(course => (
            <Grid key={course.id} item xs={4} sm={3} md={3}>
              <CourseCard course={course} />
            </Grid>
          ))}
        </>
      )}
    </Grid>
  );
}
