import { useEffect } from 'react';

/**
 * @param {'igetcool' | 'vistopia' | CourseEntity} platform
 */
export const useDescription = platform => {
  useEffect(() => {
    const description = document.querySelector('[name="description"]');
    if (platform === 'igetcool') {
      description.setAttribute('content', '天下名师 服务一个孩子');
    } else if (platform === 'vistopia') {
      description.setAttribute('content', '看见另一种可能');
    } else if (platform && platform.brief) {
      description.setAttribute('content', platform.brief);
    }
  }, [platform]);
};
