import { useEffect } from 'react';

/**
 * @param {'igetcool' | 'vistopia'} platform
 */
export const usePageFavicon = platform => {
  useEffect(() => {
    const favicons = Array.from(document.querySelectorAll('[rel="icon"],[rel="apple-touch-icon"]'));
    favicons.forEach(favicon => {
      favicon.setAttribute('href', `/yuer/${platform}.jpg`);
    });

    const description = document.querySelector('[name="description"]');
    if (platform === 'igetcool') {
      description.setAttribute('content', '天下名师 服务一个孩子');
    }
    if (platform === 'vistopia') {
      description.setAttribute('content', '看见另一种可能');
    }
  }, [platform]);
};
