import { useRef } from 'react';
import { useEffectOnce } from 'react-use';
import { useHistory } from 'react-router-dom';
import { makeStyles } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import CardActionArea from '@material-ui/core/CardActionArea';
import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';
import Typography from '@material-ui/core/Typography';
import { SECRET_QUERY_PARAM } from '@src/configs/env';

const useStyles = makeStyles(theme => ({
  media: {
    minHeight: 150,
    maxHeight: 300,
  },
  content: {
    height: 52,
    padding: '4px 8px !important',
  },
  title: {
    fontSize: theme.typography.pxToRem(14),
    fontWeight: theme.typography.fontWeightBold,
    display: ' -webkit-box',
    lineClamp: 2,
    boxOrient: 'vertical',
  },
}));

/**
 * @param {object} props
 * @param {CourseEntity} props.course
 */
export function CourseCard({ course }) {
  const classes = useStyles();
  const history = useHistory();
  const imgRef = useRef(/** @type {HTMLImageElement} */ (null));

  useEffectOnce(() => {
    const io = new IntersectionObserver(
      ([entry]) => {
        if (entry.isIntersecting) {
          const image = /** @type {HTMLImageElement} */ (entry.target);
          image.src = course.poster;

          io.unobserve(entry.target);
          io.disconnect();
        }
      },
      { rootMargin: '0px' },
    );

    io.observe(imgRef.current);

    return () => {
      io.disconnect();
    };
  });

  return (
    <Card elevation={1}>
      <CardActionArea
        onClick={() => {
          history.push(`/igetcool/courses/${course.fuzzyId}?${SECRET_QUERY_PARAM}`);
        }}
      >
        <CardMedia
          ref={imgRef}
          component="img"
          /** lazy load */
          image={''}
          className={classes.media}
          referrerPolicy="no-referrer"
        />
        <CardContent className={classes.content}>
          <Typography variant="caption" component="div" className={classes.title}>
            {course.title}
          </Typography>
        </CardContent>
      </CardActionArea>
    </Card>
  );
}
