import { useMemo, useCallback } from 'react';
import { convertToEntityList } from '@src/lib';
import { useGlobalState } from '@src/components';

/**
 * @param {object} options
 * @param {'igetcool' | 'vistopia'} options.platform
 * @param {EntityCollection<CourseEntity>} options.courseCollection
 */
export function useCourseKeyword({ platform, courseCollection }) {
  const [globalState, dispathAction] = useGlobalState();
  const { courseKeywordMap } = globalState;

  const courseKeyword = useMemo(() => {
    return String(courseKeywordMap[platform] || '').trim();
  }, [platform, courseKeywordMap]);

  const getCourseEntityList = useCallback(
    (/** @type {string} */ keyword) => {
      let entityList = convertToEntityList(courseCollection).sort((a, b) =>
        b.id.padStart(18, '0').localeCompare(a.id.padStart(18, '0')),
      );

      if (keyword) {
        entityList = entityList.filter(x => x.title.includes(keyword) || x.brief.includes(keyword));
      }

      return entityList;
    },
    [courseCollection],
  );

  const updateCourseKeyword = useCallback(
    (/** @type {string} */ keyword) => {
      dispathAction({
        type: 'updateCourseKeyword',
        payload: { platform, keyword },
      });
    },
    [platform, dispathAction],
  );

  return { courseKeyword, updateCourseKeyword, getCourseEntityList };
}
