import { useMemo } from 'react';
import { HashRouter as Router, Switch, Route, Redirect } from 'react-router-dom';
import { ThemeProvider, createMuiTheme } from '@material-ui/core/styles';
import CssBaseline from '@material-ui/core/CssBaseline';
import { useTheme } from '@src/hooks';
import { GlobalStateProvider } from '@src/components';
import { HomePage } from './routes/HomePage';
import { IndexPage as IGetCoolIndexPage } from './routes/igetcool/index';
import { DetailPage as IGetCoolDetailPage } from './routes/igetcool/detail';
import { IndexPage as VistopiaIndexPage } from './routes/vistopia/index';
import { DetailPage as VistopiaDetailPage } from './routes/vistopia/detail';

function App() {
  const { theme: themeConfig } = useTheme();
  const theme = useMemo(() => createMuiTheme(themeConfig), [themeConfig]);

  return (
    <GlobalStateProvider>
      <ThemeProvider theme={theme}>
        <CssBaseline />
        <Router>
          <Switch>
            <Route strict exact path="/">
              <HomePage />
            </Route>
            <Route strict exact path="/igetcool/index">
              <IGetCoolIndexPage />
            </Route>
            <Route strict exact path="/igetcool/courses/:courseId">
              <IGetCoolDetailPage />
            </Route>
            <Route strict exact path="/vistopia/index">
              <VistopiaIndexPage />
            </Route>
            <Route strict exact path="/vistopia/courses/:courseId">
              <VistopiaDetailPage />
            </Route>
            <Redirect to="/" />
          </Switch>
        </Router>
      </ThemeProvider>
    </GlobalStateProvider>
  );
}

export { App };
