import { useState, useCallback, useRef, useMemo } from 'react';
import { useMount } from 'react-use';
import { Redirect, useParams, useHistory } from 'react-router-dom';
import { makeStyles } from '@material-ui/core/styles';
import Container from '@material-ui/core/Container';
import Divider from '@material-ui/core/Divider';
import { VISTOPIA_DEFAULT_TITLE } from '@src/configs/env';
import { courseCollection } from '@src/configs/courses/vistopia';
import { delay, decrypt } from '@src/lib';
import { useDocumentTitle, usePageFavicon, useDescription, useSecret } from '@src/hooks';
import { TitleBar, Loading, SectionSelect, PlaybackRateSwitch } from '@src/components';
import { Heading } from './Heading';
import { SectionList } from './SectionList';

const { entities: courseEntities } = courseCollection;

const useStyles = makeStyles(theme => ({
  actionIcon: {
    marginRight: theme.spacing(2),
  },
}));

export function DetailPage() {
  const classes = useStyles();
  const history = useHistory();
  const { knowSecret } = useSecret();

  // @ts-ignore
  const { courseId: encryptedId } = useParams();
  const courseId = useMemo(() => decrypt(encryptedId), [encryptedId]);

  const tryCountRef = useRef(0);
  const [fetchStatus, setFetchStatus] = useState(/** @type {LoadingState} */ ('none'));
  const [sectionList, setSectionList] = useState(/** @type {SectionEntity[]} */ ([]));

  const fetchSectionList = useCallback(async () => {
    try {
      if (!courseEntities[courseId]) {
        setFetchStatus('failed');
        return;
      }

      setFetchStatus('loading');
      tryCountRef.current += 1;
      const { default: sectionList } = await import(`@src/data/vistopia/courses/${courseId}.json`);

      setSectionList(sectionList);
      setFetchStatus('loaded');
    } catch (err) {
      if (tryCountRef.current >= 10) {
        setFetchStatus('failed');
      } else {
        await delay(200);
        fetchSectionList();
      }
    }
  }, [courseId]);

  const sectionEntityList = useMemo(() => {
    if (sectionList.length <= 1) {
      return [];
    }

    /** @type {SectionEntity[]} */
    const entityList = [];

    let total = 0;
    for (const entity of sectionList) {
      entityList.push({
        ...entity,
        sectionTitle: `${entity.sectionTitle} (${entity.list.length} 篇)`,
      });

      total += entity.list.length;
    }

    entityList.unshift({
      sectionPid: 'all',
      sectionTitle: `全部章节 (共 ${total} 篇)`,
      list: [],
    });

    return entityList;
  }, [sectionList]);

  useDocumentTitle(
    [courseEntities[courseId]?.title, VISTOPIA_DEFAULT_TITLE].filter(Boolean).join(' - '),
  );
  usePageFavicon('vistopia');
  useDescription(courseEntities[courseId]);

  useMount(fetchSectionList);

  if (fetchStatus === 'failed') {
    return <Redirect to={knowSecret ? '/vistopia/index' : '/'} />;
  }

  return (
    <>
      <TitleBar
        title={VISTOPIA_DEFAULT_TITLE}
        course={courseEntities[courseId]}
        goBack={() => {
          history.replace('/vistopia/index');
        }}
        actions={
          <>
            {sectionEntityList.length > 0 && (
              <div className={classes.actionIcon}>
                <SectionSelect
                  course={courseEntities[courseId]}
                  sectionEntityList={sectionEntityList}
                />
              </div>
            )}
            <div className={classes.actionIcon}>
              <PlaybackRateSwitch />
            </div>
          </>
        }
      />
      <Container maxWidth="sm">
        {fetchStatus === 'loading' && <Loading />}
        {fetchStatus === 'loaded' && (
          <>
            {Boolean(courseEntities[courseId]) && (
              <>
                <Heading course={courseEntities[courseId]} />
                <Divider />
                <SectionList course={courseEntities[courseId]} sectionList={sectionList} />
              </>
            )}
          </>
        )}
      </Container>
    </>
  );
}
