import { useState, useMemo, useCallback, useEffect, useRef } from 'react';
import { useUpdateEffect } from 'react-use';
import Sound from 'react-sound';
import { makeStyles } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import CardHeader from '@material-ui/core/CardHeader';
import CardActions from '@material-ui/core/CardActions';
import Divider from '@material-ui/core/Divider';
import Link from '@material-ui/core/Link';
import IconButton from '@material-ui/core/IconButton';
import DescriptionOutlinedIcon from '@material-ui/icons/DescriptionOutlined';
import Typography from '@material-ui/core/Typography';
import PlayArrowIcon from '@material-ui/icons/PlayArrow';
import ReplayIcon from '@material-ui/icons/Replay';
import PauseIcon from '@material-ui/icons/Pause';
import FastRewindIcon from '@material-ui/icons/FastRewind';
import FastForwardIcon from '@material-ui/icons/FastForward';
import CircularProgress from '@material-ui/core/CircularProgress';
import { useSecret } from '@src/hooks';
import { moment, scrollIntoView } from '@src/lib';
import { EE } from '@src/utils';
import { useGlobalState } from '@src/components';

const useStyles = makeStyles(theme => ({
  root: {
    position: 'relative',
  },
  header: {
    paddingTop: 12,
    paddingBottom: 0,
  },
  title: {
    fontSize: theme.typography.pxToRem(14),
    fontWeight: theme.typography.fontWeightBold,
  },
  buffer: {
    position: 'absolute',
    height: '100%',
    left: 0,
    top: 0,
    transition: 'width 200ms ease-out',
    opacity: 0.1,
    background: theme.palette.secondary.main,
  },
  progress: {
    position: 'absolute',
    height: '100%',
    left: 0,
    top: 0,
    transition: 'width 200ms ease-out',
    opacity: 0.15,
    background: theme.palette.secondary.main,
  },
  duration: {
    position: 'absolute',
    top: '50%',
    transform: 'translateY(-50%)',
    right: 12,
  },
  action: {
    position: 'relative',
    height: 38,
    paddingTop: 4,
    paddingBottom: 4,
  },
}));

/**
 * @param {object} props
 * @param {AudioEntity} props.audio
 */
export function AudioCard({ audio }) {
  const rootRef = useRef(/** @type {HTMLElement} */ (null));
  const classes = useStyles();
  const [globalState, dispathAction] = useGlobalState();

  const { playingMediaId } = globalState;
  const { knowSecret } = useSecret();

  const [playStatus, setPlayStatus] = useState(/** @type {SoundPlayStatus} */ ('STOPPED'));
  const [finished, setFinished] = useState(false);

  const [byteLoaded, setBytesLoaded] = useState(0);
  const [bytesTotal, setBytesTotal] = useState(0);
  const [waitBuffer, setWaitBuffer] = useState(false);

  const [position, setPosition] = useState(0);
  const [duration, setDuration] = useState(audio.audioDuration * 1000 || 0);

  const bufferPercent = useMemo(() => {
    if (!bytesTotal) {
      return 0;
    }
    return (100 * byteLoaded) / bytesTotal;
  }, [byteLoaded, bytesTotal]);

  const progressPercent = useMemo(() => {
    if (!duration) {
      return 0;
    }
    return finished ? 100 : (100 * position) / duration;
  }, [finished, position, duration]);

  const remainDuration = useMemo(() => {
    if (!duration) {
      return '--:--';
    }

    const remain = moment.duration(finished ? 0 : Math.max(0, duration - position));
    const parts = [remain.minutes(), remain.seconds()];
    if (remain.hours() > 0) {
      parts.unshift(remain.hours());
    }
    return parts.map(x => String(x).padStart(2, '0')).join(':');
  }, [finished, position, duration]);

  const updatePosition = useCallback(({ position }) => {
    setPosition(position);
  }, []);

  // 广播当前正在播放的音频
  const notifyPlayAudio = useCallback(() => {
    dispathAction({ type: 'switchPlayingMediaId', payload: { id: audio.audioPid } });
  }, [audio, dispathAction]);

  const notifyPauseAudio = useCallback(() => {
    dispathAction({ type: 'switchPlayingMediaId', payload: { id: null } });
  }, [dispathAction]);

  // 音频互斥播放
  useUpdateEffect(() => {
    if (audio.audioPid === playingMediaId) {
      if (playStatus !== 'PLAYING') {
        setPlayStatus('PLAYING');
      }
    } else {
      if (playStatus === 'PLAYING') {
        setPlayStatus('PAUSED');
      }
    }
  }, [audio, playStatus, playingMediaId]);

  // 播放下一个音频
  useEffect(() => {
    const playNextAudio = ({ id }) => {
      if (id === audio.audioPid) {
        if (finished) {
          setFinished(false);
          setPosition(0);
        }

        notifyPlayAudio();

        scrollIntoView(rootRef.current, {
          scrollMode: 'if-needed',
          block: 'nearest',
          inline: 'nearest',
          behavior: 'smooth',
        });
      }
    };

    EE.on('replyNextPlayingMediaId', playNextAudio);

    return () => {
      EE.off('replyNextPlayingMediaId', playNextAudio);
    };
  }, [audio, finished, notifyPlayAudio]);

  // 快退
  const doFastRewind = useCallback(() => {
    // -5%
    const newProgressPercent = Math.max(0, progressPercent - 10);
    const newPosition = (duration * newProgressPercent) / 100;

    setPosition(Math.max(newPosition, position - 60 * 1e3));

    if (finished) {
      setFinished(false);
    }
    if (!playingMediaId) {
      setPlayStatus('PLAYING');
    }
    notifyPlayAudio();
  }, [finished, position, duration, progressPercent, notifyPlayAudio, playingMediaId]);

  // 快进
  const doFastForward = useCallback(() => {
    if (progressPercent >= 100) {
      setPosition(0);
    } else {
      // +5%
      const newProgressPercent = Math.min(progressPercent + 5, 100);
      const newPosition = Math.min((duration * newProgressPercent) / 100, duration);
      setPosition(Math.min(newPosition, position + 60 * 1e3));
    }

    if (finished) {
      setFinished(false);
    }
    if (!playingMediaId) {
      setPlayStatus('PLAYING');
    }
    notifyPlayAudio();
  }, [finished, position, duration, progressPercent, notifyPlayAudio, playingMediaId]);

  useUpdateEffect(() => {
    if (playStatus === 'PLAYING') {
      if (
        progressPercent === 0 ||
        (bufferPercent > 0 && bufferPercent < 100 && progressPercent === bufferPercent)
      ) {
        setWaitBuffer(true);
      } else {
        setWaitBuffer(false);
      }
    } else {
      setWaitBuffer(false);
    }
  }, [playStatus, progressPercent, bufferPercent]);

  return (
    <>
      <Card ref={rootRef} elevation={0} square className={classes.root}>
        <Divider />
        <div
          className={classes.buffer}
          style={{ width: `${Math.max(progressPercent, bufferPercent)}%` }}
        />
        <div className={classes.progress} style={{ width: `${progressPercent}%` }} />
        <CardHeader
          className={classes.header}
          action={
            knowSecret && (
              <Link href={audio.audioArticleUrl} target="_blank" rel="noreferrer">
                <IconButton size="small">
                  <DescriptionOutlinedIcon />
                </IconButton>
              </Link>
            )
          }
          title={
            <Typography variant="h6" color="textSecondary" className={classes.title}>
              {audio.audioTitle}
            </Typography>
          }
        />

        <CardActions className={classes.action}>
          <IconButton
            size="small"
            onClick={() => {
              if (playStatus === 'PLAYING') {
                notifyPauseAudio();
              } else {
                if (finished) {
                  setFinished(false);
                  setPosition(0);
                }
                if (!playingMediaId) {
                  setPlayStatus('PLAYING');
                }
                notifyPlayAudio();
              }
            }}
          >
            {playStatus === 'PLAYING' && (
              <>
                {waitBuffer && <CircularProgress size={24} color="secondary" disableShrink />}
                {!waitBuffer && <PauseIcon fontSize="default" color="action" />}
              </>
            )}
            {playStatus !== 'PLAYING' && (
              <>
                {finished && <ReplayIcon fontSize="default" color="action" />}
                {!finished && <PlayArrowIcon fontSize="default" color="action" />}
              </>
            )}
          </IconButton>
          <div />
          <IconButton size="small" onClick={doFastRewind}>
            <FastRewindIcon fontSize="small" color="action" />
          </IconButton>
          <IconButton size="small" onClick={doFastForward}>
            <FastForwardIcon fontSize="small" color="action" />
          </IconButton>
          <Typography
            variant="inherit"
            display="block"
            noWrap
            color="textSecondary"
            className={classes.duration}
          >
            {remainDuration}
          </Typography>
        </CardActions>
      </Card>
      {playStatus === 'PLAYING' && (
        <Sound
          url={audio.audioUrl}
          playStatus={playStatus}
          autoLoad={false}
          loop={false}
          playbackRate={globalState.playbackRate}
          position={position}
          onLoading={info => {
            setBytesLoaded(
              Math.min(
                (info.bytesTotal * progressPercent) / 100 + info.bytesLoaded,
                info.bytesTotal,
              ),
            );
            setBytesTotal(info.bytesTotal);
            setDuration(duration ? Math.min(duration, info.duration) : info.duration);
          }}
          onPlaying={updatePosition}
          onFinishedPlaying={() => {
            setPlayStatus('STOPPED');
            setFinished(true);
            EE.emit('requestNextPlayingMediaId');
          }}
          onError={({ errorCode, description }) => {
            console.log('error', { errorCode, description });
          }}
        />
      )}
    </>
  );
}
