import { useMemo, useState, useRef } from 'react';
import { useUpdateEffect } from 'react-use';
import classNames from 'classnames';
import { useTheme } from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';
import Drawer from '@material-ui/core/Drawer';
import Badge from '@material-ui/core/Badge';
import IconButton from '@material-ui/core/IconButton';
import ViewListIcon from '@material-ui/icons/ViewList';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import Typography from '@material-ui/core/Typography';
import CheckCircleIcon from '@material-ui/icons/CheckCircle';
import { useCourseSection } from '@src/hooks';
import { scrollIntoView } from '@src/lib';

const useStyles = makeStyles(theme => ({
  drawer: {
    '& .MuiPaper-root': {
      minWidth: 250,
      maxWidth: '85%',
    },
  },
  text: {
    overflow: 'hidden',
    whiteSpace: 'nowrap',
    textOverflow: 'ellipsis',
    fontSize: theme.typography.pxToRem(14),
    fontWeight: theme.typography.fontWeightBold,
  },
  icon: {
    minWidth: 'auto',
  },
}));

/**
 * @param {object} props
 * @param {CourseEntity} props.course
 * @param {SectionEntity[]} props.sectionEntityList
 */
export function SectionSelect({ course, sectionEntityList }) {
  const theme = useTheme();
  const classes = useStyles();
  const drawerRef = useRef(/** @type {HTMLElement} */ (null));

  const [open, setOpen] = useState(false);
  const { selectedSectionId, updateSelectedSectionId } = useCourseSection({
    course,
    sectionList: sectionEntityList,
  });

  useUpdateEffect(() => {
    if (open && drawerRef.current) {
      const selectedListItem = drawerRef.current.querySelector('.selected');

      if (selectedListItem) {
        setTimeout(() => {
          scrollIntoView(selectedListItem, {
            scrollMode: 'if-needed',
            block: 'center',
            inline: 'center',
            behavior: 'smooth',
          });
        }, 300);
      }
    }
  }, [open]);

  const listItems = useMemo(() => {
    return (
      <List component="div">
        {sectionEntityList.map(section => {
          const selected = section.sectionPid === selectedSectionId;

          return (
            <ListItem
              key={section.sectionPid}
              button
              selected={selected}
              className={classNames({ selected })}
              onClick={() => {
                updateSelectedSectionId(section.sectionPid);
                setOpen(false);

                if (section.sectionPid !== selectedSectionId) {
                  window.scrollTo(0, 0);
                }
              }}
            >
              <ListItemText
                primary={
                  <Typography
                    className={classes.text}
                    color={
                      selected
                        ? theme.palette.type === 'light'
                          ? 'primary'
                          : 'textPrimary'
                        : theme.palette.type === 'light'
                        ? 'inherit'
                        : 'textSecondary'
                    }
                  >
                    {section.sectionTitle}
                  </Typography>
                }
              />
              <ListItemIcon className={classes.icon}>
                <CheckCircleIcon
                  color={
                    selected ? (theme.palette.type === 'light' ? 'primary' : 'inherit') : 'disabled'
                  }
                />
              </ListItemIcon>
            </ListItem>
          );
        })}
      </List>
    );
  }, [
    theme,
    classes.text,
    classes.icon,
    sectionEntityList,
    selectedSectionId,
    updateSelectedSectionId,
  ]);

  return (
    <>
      <IconButton
        edge="start"
        color="inherit"
        onClick={() => {
          setOpen(true);
        }}
      >
        <Badge badgeContent={selectedSectionId === 'all' ? null : '✓'} color="primary">
          <ViewListIcon />
        </Badge>
      </IconButton>
      <Drawer
        ref={drawerRef}
        anchor="left"
        variant="temporary"
        open={open}
        keepMounted
        className={classes.drawer}
        onClose={() => setOpen(false)}
      >
        {listItems}
      </Drawer>
    </>
  );
}
