import { makeStyles } from '@material-ui/core/styles';
import { useMount, useUpdateEffect, useWindowScroll } from 'react-use';
import { IGETCOOL_DEFAULT_TITLE } from '@src/configs/env';
import { courseCollection } from '@src/configs/courses/igetcool';
import Container from '@material-ui/core/Container';
import { useDocumentTitle, usePageFavicon, useDescription } from '@src/hooks';
import { TitleBar, CourseSearch } from '@src/components';
import { CourseList } from './CourseList';

const useStyles = makeStyles(theme => ({
  container: {
    paddingLeft: '8px',
    paddingRight: '8px',
  },
  actionIcon: {
    marginRight: theme.spacing(2),
  },
}));

let pageScrollTop = 0;

export function IndexPage() {
  const classes = useStyles();
  const { y } = useWindowScroll();

  useDocumentTitle(IGETCOOL_DEFAULT_TITLE);
  usePageFavicon('igetcool');
  useDescription('igetcool');

  useUpdateEffect(() => {
    pageScrollTop = y;
  }, [y]);

  useMount(() => {
    document.documentElement.scrollTop = pageScrollTop;
  });

  return (
    <>
      <TitleBar
        title={IGETCOOL_DEFAULT_TITLE}
        actions={
          <div className={classes.actionIcon}>
            <CourseSearch platform="igetcool" courseCollection={courseCollection} />
          </div>
        }
      />
      <Container maxWidth="md" className={classes.container}>
        <CourseList />
      </Container>
    </>
  );
}
