import { forwardRef, useRef, useState, useCallback, useMemo } from 'react';
import { useHistory } from 'react-router-dom';
import { makeStyles } from '@material-ui/core/styles';
import Badge from '@material-ui/core/Badge';
import SearchIcon from '@material-ui/icons/Search';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import IconButton from '@material-ui/core/IconButton';
import CloseIcon from '@material-ui/icons/Close';
import Slide from '@material-ui/core/Slide';
import Container from '@material-ui/core/Container';
import TextField from '@material-ui/core/TextField';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';
import Typography from '@material-ui/core/Typography';
import { useCourseKeyword } from '@src/hooks';
import { SECRET_QUERY_PARAM } from '@src/configs/env';

const useStyles = makeStyles(theme => ({
  appBar: {
    position: 'fixed',
  },
  title: {
    fontWeight: theme.typography.fontWeightBold,
    marginLeft: theme.spacing(2),
    flex: 1,
  },
  clear: {
    fontSize: theme.typography.pxToRem(16),
    fontWeight: theme.typography.fontWeightBold,
  },
  okay: {
    fontSize: theme.typography.pxToRem(16),
    fontWeight: theme.typography.fontWeightBold,
  },
  input: {
    marginTop: theme.spacing(1),
  },
  courseTitle: {
    fontWeight: theme.typography.fontWeightBold,
  },
  offset: theme.mixins.toolbar,
}));

// @ts-ignore
const Transition = forwardRef((props, ref) => <Slide direction="up" ref={ref} {...props} />);

/**
 * @param {object} props
 * @param {'igetcool' | 'vistopia'} props.platform
 * @param {EntityCollection<CourseEntity>} props.courseCollection
 */
export function CourseSearch({ platform, courseCollection }) {
  const history = useHistory();
  const classes = useStyles();
  const { courseKeyword, updateCourseKeyword, getCourseEntityList } = useCourseKeyword({
    platform,
    courseCollection,
  });

  const inputRef = useRef(/** @type {HTMLInputElement} */ (null));
  const [open, setOpen] = useState(false);
  const [keyword, setKeyword] = useState(courseKeyword);

  const courseEntityList = useMemo(() => getCourseEntityList(keyword), [
    keyword,
    getCourseEntityList,
  ]);

  const handleClickOpen = useCallback(() => setOpen(true), []);

  const handleClose = useCallback(() => {
    setKeyword(courseKeyword);
    setOpen(false);
  }, [courseKeyword]);

  const handleClear = useCallback(() => {
    setKeyword('');
    inputRef.current.focus();
  }, []);

  const handleOkay = useCallback(() => {
    updateCourseKeyword(keyword);
    setOpen(false);
  }, [keyword, updateCourseKeyword]);

  return (
    <>
      <IconButton edge="start" color="inherit" onClick={handleClickOpen}>
        <Badge badgeContent={!courseKeyword ? null : '✓'} color="primary">
          <SearchIcon />
        </Badge>
      </IconButton>
      <Dialog
        fullScreen
        open={open}
        onClose={handleClose}
        // @ts-ignore
        TransitionComponent={Transition}
      >
        <AppBar className={classes.appBar}>
          <Toolbar>
            <IconButton edge="start" color="inherit" onClick={handleClose}>
              <CloseIcon />
            </IconButton>
            <Typography variant="h6" className={classes.title}>
              {'搜索课程'}
            </Typography>
            <Button color="inherit" variant="text" className={classes.clear} onClick={handleClear}>
              {'清空'}
            </Button>
            <Button color="inherit" variant="text" className={classes.okay} onClick={handleOkay}>
              {'确定'}
            </Button>
          </Toolbar>
        </AppBar>

        {/* @see https://material-ui.com/components/app-bar/#fixed-placement */}
        <div className={classes.offset} />

        <Container>
          <TextField
            inputRef={inputRef}
            label="关键词"
            value={keyword}
            onChange={evt => setKeyword(evt.target.value)}
            color="secondary"
            fullWidth
            autoFocus
            className={classes.input}
          />
          <List>
            {courseEntityList.length === 0 && (
              <ListItem>
                <ListItemText secondary={'未搜索到课程'} />
              </ListItem>
            )}

            {courseEntityList.length > 0 &&
              courseEntityList.map(course => (
                <ListItem key={course.id} button>
                  <ListItemText
                    primary={
                      <Typography className={classes.courseTitle}>{course.title}</Typography>
                    }
                    secondary={course.brief}
                    onClick={() => {
                      updateCourseKeyword(keyword.trim() || course.title);
                      setOpen(false);
                      history.push(`/${platform}/courses/${course.fuzzyId}?${SECRET_QUERY_PARAM}`);
                    }}
                  />
                </ListItem>
              ))}
          </List>
        </Container>
      </Dialog>
    </>
  );
}
